.swiper-container {
    max-width: 1400px;
    height: 40%!important;
  
  }
  .swiper-slide {
    position: relative;
    text-align: center;
    font-size: 18px;
    background: #fff;
  
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;

    img{
      width:100%;
    }


  }

$bkg-clr: #0000005d;

  .slide-container{
    color:white;
    position: absolute;
    z-index: index($elements, slider);
    width: 80%;
    opacity: 0;
    transition: opacity 350ms ease-in 100ms;
    
    h1{
      background: $bkg-clr;
      font-size: 20px;
      opacity: 0;
      transform: translateY(50px);
      transition: opacity 350ms ease-in 500ms, transform 350ms ease-in 500ms;
    }
    p:nth-child(2){
      background: $bkg-clr;
      opacity: 0;
      color:red;
      transform: translateX(-50px);
      transition: opacity 350ms ease-in 950ms, transform 350ms ease-in 950ms;

    }
  }
  
  
  .swiper-slide-active  .slide-container{
    opacity: 1;
    transform: translateY(0px);
    
    h1{
      opacity: 1;
      transform: translateY(0px);
    }
    p:nth-child(2){
      opacity: 1;
      transform: translateX(0px);
  } 
  .caption-container{
    // display:flex;

    &__sub-caption{
      margin: 5px;
      display: flex;
      
      span{
        display: inline-block;
        margin: 5px;
        background-color:$bkg-clr;
      }
    }

  }
}